@font-face {
    font-family: 'Yahoo';
    src: url('./yahoo/Yahoo.ttf') format('truetype'); /* Adjust the format if necessary */
    font-weight:bolder;
    font-style: normal;
  }
  .yahooheader{
   font-family: 'Yahoo', sans-serif;
   color:blueviolet;
   font-weight: bolder;
   font-size: 29px;
   padding-left:40px; 
  }
  .error-message{
    color:red;
    font-weight: bold;
  }
  .stay-signed-in{
  color:blueviolet;
  }
  .container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  }
  
  .content {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  }
  
  .writeups {
  max-width:1100px;
  margin-right: 20px;
  font-size:28px;
  }
  .writeup{
  font-weight:bolder;
  font-size:35px;
  }
  .horizontal{
  display: flex;
  justify-content: space-between;
  align-items: center; /* Optional: vertically center the items */ 
  color:blueviolet;
  }
  .line{
  display:flex;
  gap: 10px;
  cursor:pointer;
  }
  .yahoohd{
  font-family: 'Yahoo', sans-serif;
  color:blueviolet;
  font-weight: bolder;
  font-size: 18px;
  }
  .container{
  display:flex;
  }
  
  .box{
  background-color: rgb(255, 255, 255);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 30px;
  border-radius: 20px;
  width: 400px;
  margin: 20px auto;
  height:770px;
  text-align: center;
  float:left;
  
  }
  .signin{
  font-weight: bolder;
  font-size: 30px;
  }
  input {
  width: 100%;
  padding: 10px;
  border: none;
  border-Bottom: 2px solid blueviolet; 
  outline: 'none';
  cursor:pointer;
  }
  .continue-button {
  background-color:blueviolet; /* Green */
  border: none;
  color:white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  width:400px;
  height:60px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius:30px;
  transition: background-color 0.3s ease;
  }
  
  .continue-button:hover {
  background-color:rgb(133, 75, 133);
  }
  
  .login-form {
  display: flex;
  align-items: center;
  justify-content:space-between;
  }
  .login-form {
  display: flex;
  align-items: center;
  }
  
  .checkbox {
  margin-right: 10px;
  }
      
  
  .forget-username {
  text-decoration: none;
  color:blueviolet;
  }
  
  .continue-button2{
  background-color:white; /* Green */
  border-color:blueviolet;
  color: blueviolet;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 20px;
  width:400px;
  height:50px;
  cursor: pointer;
  border-radius:30px;
  transition: background-color 0.3s ease;
  }
  
  
  .continue-button3{
  background-color:white; /* Green */
  border-color:grey;
  color: black;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 20px;
  width:400px;
  height:50px;
  cursor: pointer;
  border-radius:30px;
  transition: background-color 0.3s ease;
  justify-content: space-between;
  }
  
  .googleimg{
  width:36px;
  padding-right:40px;
  
  }
  
@media (max-width: 667px) {
  .writeup, .writeup2 {
    display:none;
  }

  .line{
    display:none;
    
    }
    .yahooheader{
      display:none;
     }
  
}

  
  
  
  
  
  
  
  
  
  
  
  
  